.dashboard {
    display: flex;
    min-height: 100vh;
    width: 100%;
    animation: fadeIn 0.4s ease-in-out;
    &__controller {
        color: #fefefe;
        flex: 2;
        max-width: 250px;
        padding: 20px;
        background: #636363;
        & > * {
            margin-bottom: 20px;
        }
    }

    &__main {
        padding: 20px;
        flex: 5;
    }

    .controller {
        &__menu {
            &.active {
                img {
                    transform: rotate(180deg);
                }

                .controller__dropdown {
                    max-height: 500px;
                }
            }
        }
        &__title {
            letter-spacing: 1px;
        }

        &__divider {
            display: block;
            opacity: 0.5;
            border: 1px solid #fefefe;
        }

        &__section {
            cursor: pointer;
            align-items: center;
            display: flex;
            h4 {
                letter-spacing: 1px;
                margin-right: 5px;
                font-weight: 550;
            }

            img {
                transition: 0.5s ease-in-out;
            }
        }

        &__dropdown {
            margin-top: 5px;
            padding-left: 15px;
            max-height: 0;
            overflow: hidden;
            transition: 0.5s ease-in-out;

            & > * {
                margin-bottom: 5px;
            }

            .dropdown {
                &__column {
                    a {
                        display: block;
                        padding: 5px 10px;
                        opacity: 0.8;
                        transition: 0.2s ease-in-out;
                        color: $primary-color-font;
                        &:hover {
                            opacity: 1;
                            color: $support-color-main;
                        }
                    }
                }
            }
        }
    }

    .dash {
        &__role {
            @include FullDimension;
        }
    }
}
